* {
  margin: 0;
  padding: 0;
}

.page-main {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #20212B;
  height: 100vh;
}

.wrapper {
  width: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.score {
  width: 200px;
  height: 47px;
  background-color: #1E0000;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

#score {
  margin-bottom: 0;
  color: #ED2D77;
  font-size: 24px;
  line-height: 29px;
  font-weight: 600;
  font-family: Arial, Helvetica, sans-serif;
}

.canvas-wrapper {
  margin-bottom: 20px;
  width: 380px;
  height: 340px;
  border: 15px solid #1E0000;
  border-radius: 10px;
  background-image: url(../img/background.png);
  background-repeat: no-repeat;
  background-position: 0 0;
}

.button {
  width: 180px;
  height: 47px;
  background-color: #1E0000;
  border: none;
  border-radius: 5px;
  color: #ED2D77;
  font-size: 24px;
  line-height: 29px;
  font-weight: 600;
  font-family: Arial, Helvetica, sans-serif;
}

.button-wrapper {
  margin-bottom: 20px;
}

.game-difficulty p {
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #ED2D77;
  margin-bottom: 20px;
}